import { Grid, Typography, Box, Button } from "@mui/material";
import { ReactComponent as Comments } from "./assets/icon-comments.svg";
import { ReactComponent as ArrowUp } from "./assets/icon-arrow-up.svg";
import React from "react";
import { CategoryButton, UpvoteButton } from "./StyledButton";

export default function DesktopFeedbacks({
  data,
  sortBy,
  onClickCategory,
  onClickUpvote,
  onClickFeedback,
}) {
  return (
    <Grid
      container
      sx={{
        background: "white",
        padding: 3,
        margin: "1.5rem 0",
        borderRadius: "10px",
      }}
    >
      <Grid
        item
        xs
        sx={{
          textAlign: "left",
        }}
      >
        <UpvoteButton value={sortBy} onClick={() => onClickUpvote(data.id)}>
          <ArrowUp />
          {data.upvotes}
        </UpvoteButton>
      </Grid>
      <Grid item xs={9} sx={{ textAlign: "left" }}>
        <Box onClick={() => onClickFeedback(data)}>
          <Typography sx={{ pb: "10px" }}>
            <b>{data.title}</b>
          </Typography>
          <Typography sx={{ pb: "10px", color: "#647196" }}>
            {data.description}
          </Typography>
        </Box>
        <CategoryButton
          variant="contained"
          value={data.category}
          onClick={onClickCategory}
        >
          {data.category === "ui" || data.category === "ux"
            ? data.category.toUpperCase()
            : data.category}
        </CategoryButton>
      </Grid>
      <Grid
        item
        xs
        sx={{
          justifyContent: "end",
          display: "flex",
          alignItems: "center",
        }}
      >
        <Button>
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
            }}
          >
            <Comments />
            &nbsp;
            {data.comments !== undefined ? data.comments.length : 0}
          </Box>
        </Button>
      </Grid>
    </Grid>
  );
}
