import { Grid, Typography, Box } from "@mui/material";
import React, { useReducer, useEffect } from "react";
import { VscCircleFilled } from "react-icons/vsc";
import { IconContext } from "react-icons";
import { Link } from "react-router-dom";
import { ReactComponent as Comments } from "./assets/icon-comments.svg";
import { ReactComponent as Plus } from "./assets/icon-plus.svg";
import { ReactComponent as ArrowLeft } from "./assets/icon-arrow-left.svg";
import { ReactComponent as ArrowUp } from "./assets/icon-arrow-up.svg";
import {
  PurpleButton,
  RoadmapCategoryButton,
  UpvoteButton,
} from "./StyledButton";
import { BulletPoint, Description, Title } from "./StyledElements";
import { styled } from "@mui/material/styles";

const MainBox = styled(Box)({
  backgroundColor: "#ffffff",
  borderRadius: "5px",
  margin: "1rem 0",
  borderTopWidth: "5px",
  borderTopStyle: "solid",
  padding: "1.5rem",
  flexDirection: "column",
  display: "flex",
});

const CustomBox = styled(Box)({
  flexDirection: "row",
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
});

const CommentBox = styled(Box)({
  flexDirection: "row",
  display: "flex",
  alignItems: "center",
});

export default function Roadmap({
  data,
  onClickMain,
  onClickCategory,
  onClickUpvote,
  onClickFeedback,
}) {
  const initialState = {
    planned: [],
    inProgress: [],
    live: [],
  };

  const reducer = (state, newState) => ({ ...state, ...newState });
  const [state, setState] = useReducer(reducer, initialState);

  useEffect(() => {
    sortData();
  }, [data]);

  const sortData = () => {
    const planned = [];
    const inProgress = [];
    const live = [];

    data.forEach((e) => {
      if (e.status === "planned") {
        planned.push(e);
      } else if (e.status === "in-progress") {
        inProgress.push(e);
      } else if (e.status === "live") {
        live.push(e);
      }
    });
    setState({ planned: planned, inProgress: inProgress, live: live });
  };

  return (
    <React.Fragment>
      <Grid
        container
        sx={{
          width: { xs: "100%", sm: "80%" },
          backgroundColor: "#3A4374",
          borderRadius: { xs: 0, sm: "10px" },
          padding: "1rem 2rem",
          margin: "4rem auto 0 auto",
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <Grid item>
          <Link onClick={onClickMain}>
            <Typography sx={{ color: "white", fontSize: "0.8rem" }}>
              <ArrowLeft stroke="white" />
              &nbsp;&nbsp;&nbsp;
              <b>Go Back</b>
            </Typography>
          </Link>
          <Typography sx={{ color: "white" }}>
            <b>Roadmap</b>
          </Typography>
        </Grid>
        <Grid item>
          <Link to="/newfeedback" state={{ data: data }}>
            <PurpleButton>
              <Plus />
              &nbsp; Add Feedback
            </PurpleButton>
          </Link>
        </Grid>
      </Grid>
      <Grid
        container
        sx={{
          width: { xs: "100%", sm: "80%" },
          margin: "auto",
          textAlign: "left",
        }}
      >
        <Grid item sm={4} sx={{ padding: "3rem 0.7rem 0 0" }}>
          <Typography sx={{ fontSize: "0.9rem" }}>
            <b>Planned ({state.planned.length})</b>
          </Typography>
          <Typography sx={{ fontSize: "0.8rem", color: "#647196", pb: 1 }}>
            Ideas prioritized for research
          </Typography>
          {state.planned.map((e, idx) => (
            <MainBox key={idx} sx={{ borderTopColor: "orange" }}>
              <BulletPoint>
                <IconContext.Provider value={{ color: "orange" }}>
                  <VscCircleFilled />
                </IconContext.Provider>
                &nbsp;&nbsp;{e.status}
              </BulletPoint>
              <Title>{e.title}</Title>
              <Description>{e.description}</Description>
              <Box sx={{ width: "auto" }}>
                <RoadmapCategoryButton
                  variant="contained"
                  value={e.category}
                  onClick={onClickCategory}
                >
                  {e.category}
                </RoadmapCategoryButton>
              </Box>
              <CustomBox>
                <UpvoteButton onClick={() => onClickUpvote(e.id)}>
                  <ArrowUp />
                  {e.upvotes}
                </UpvoteButton>
                <CommentBox onClick={() => onClickFeedback(e)}>
                  <Comments />
                  &nbsp;&nbsp;
                  <Typography>
                    {e.comments !== undefined ? e.comments.length : 0}
                  </Typography>
                </CommentBox>
              </CustomBox>
            </MainBox>
          ))}
        </Grid>
        <Grid item sm={4} sx={{ padding: "3rem 0.7rem 0 0.7rem" }}>
          <Typography sx={{ fontSize: "0.9rem" }}>
            <b>In-Progress ({state.inProgress.length})</b>
          </Typography>
          <Typography sx={{ fontSize: "0.8rem", color: "#647196", pb: 1 }}>
            Currently being developed
          </Typography>
          {state.inProgress.map((e, idx) => (
            <MainBox key={idx} sx={{ borderTopColor: "purple" }}>
              <BulletPoint>
                <IconContext.Provider value={{ color: "purple" }}>
                  <VscCircleFilled />
                </IconContext.Provider>
                &nbsp;&nbsp;{e.status}
              </BulletPoint>
              <Title>{e.title}</Title>
              <Description>{e.description}</Description>
              <Box sx={{ width: "auto" }}>
                <RoadmapCategoryButton
                  variant="contained"
                  value={e.category}
                  onClick={onClickCategory}
                >
                  {e.category}
                </RoadmapCategoryButton>
              </Box>

              <CustomBox>
                <UpvoteButton onClick={() => onClickUpvote(e.id)}>
                  <ArrowUp />
                  {e.upvotes}
                </UpvoteButton>
                <CommentBox onClick={() => onClickFeedback(e)}>
                  <Comments />
                  &nbsp;&nbsp;
                  <Typography>
                    {e.comments !== undefined ? e.comments.length : 0}
                  </Typography>
                </CommentBox>
              </CustomBox>
            </MainBox>
          ))}
        </Grid>
        <Grid item sm={4} sx={{ padding: "3rem 0 0 0.7rem" }}>
          <Typography sx={{ fontSize: "0.9rem" }}>
            <b>Live ({state.live.length})</b>
          </Typography>
          <Typography sx={{ fontSize: "0.8rem", color: "#647196", pb: 1 }}>
            Released features
          </Typography>
          {state.live.map((e, idx) => (
            <MainBox key={idx} sx={{ borderTopColor: "skyblue" }}>
              <BulletPoint>
                <IconContext.Provider value={{ color: "skyblue" }}>
                  <VscCircleFilled />
                </IconContext.Provider>
                &nbsp;&nbsp;{e.status}
              </BulletPoint>
              <Title>{e.title}</Title>
              <Description>{e.description}</Description>
              <Box sx={{ width: "auto" }}>
                <RoadmapCategoryButton
                  variant="contained"
                  value={e.category}
                  onClick={onClickCategory}
                >
                  {e.category}
                </RoadmapCategoryButton>
              </Box>

              <CustomBox>
                <UpvoteButton onClick={() => onClickUpvote(e.id)}>
                  <ArrowUp />
                  {e.upvotes}
                </UpvoteButton>
                <CommentBox onClick={() => onClickFeedback(e)}>
                  <Comments />
                  &nbsp;&nbsp;
                  <Typography>
                    {e.comments !== undefined ? e.comments.length : 0}
                  </Typography>
                </CommentBox>
              </CustomBox>
            </MainBox>
          ))}
        </Grid>
      </Grid>
    </React.Fragment>
  );
}
