import { Grid, Typography, Button, Box } from "@mui/material";
import React, { useEffect } from "react";
import { ReactComponent as Comments } from "./assets/icon-comments.svg";
import { ReactComponent as ArrowLeft } from "./assets/icon-arrow-left.svg";
import { ReactComponent as ArrowUp } from "./assets/icon-arrow-up.svg";

import { Link } from "react-router-dom";
import {
  BlueButton,
  PurpleButton,
  CategoryButton,
  UpvoteButton,
} from "./StyledButton";
import { CustomTextField } from "./StyledInput";

export default function Feedback({
  data,
  onClickMain,
  onClickUpvote,
  onClickEditFeedback,
  fetchData,
}) {
  const [comment, setComment] = React.useState("");
  const [reply, setReply] = React.useState([]);

  useEffect(() => {}, [data]);

  const onClickComment = async () => {
    let commentToPost = {
      content: comment,
      id: data.comments.length + 1,
      user: {
        image: "./assets/user-images/image-anonymous.jpg",
        name: "anonymous",
        username: "anonymous",
      },
      replies: [],
    };

    let commentStr = JSON.stringify(commentToPost);
    let myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");

    try {
      let res = await fetch(`http://localhost:8000/postComment/${data.id}`, {
        method: "PUT",
        headers: myHeaders,
        body: commentStr,
      });
      let json = await res.json();
      fetchData();
    } catch (error) {}
  };

  const onClickReply = async (id, user) => {
    const indexToAdd = reply.findIndex((rp) => rp.id.toString() === id);
    const indexToReply = data.comments.findIndex(
      (cm) => cm.id.toString() === id
    );
    let replyToPost = {
      content: reply[indexToAdd].content,
      replyingTo:
        user !== "" ? user : data.comments[indexToReply].user.username,
      user: {
        image: "./assets/user-images/image-anonymous.jpg",
        name: "anonymous",
        username: "anonymous",
      },
    };
    let commentStr = JSON.stringify(replyToPost);
    let myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    try {
      let res = await fetch(
        `http://localhost:8000/postReply/${data.id}/${data.comments[indexToReply].id}`,
        {
          method: "PUT",
          headers: myHeaders,
          body: commentStr,
        }
      );
      let json = await res.json();
      fetchData();
    } catch (error) {}
  };

  const onChangeComment = (e) => {
    if (e.target.value.length <= 255) {
      setComment(e.target.value);
    } else {
      setComment(e.target.value.slice(0, 255));
      e.target.value = e.target.value.slice(0, 255);
    }
  };

  const onChangeReply = (e) => {
    let id = e.target.id;
    const indexToRemove = reply.findIndex((rp) => rp.id.toString() === id);
    reply.splice(indexToRemove, 1);

    if (e.target.value.length <= 255) {
      setReply((oldArray) => [
        ...oldArray,
        { id: e.target.id, content: e.target.value },
      ]);
    } else {
      setReply((oldArray) => [
        ...oldArray,
        { id: e.target.id, content: e.target.value.slice(0, 255) },
      ]);
      e.target.value = e.target.value.slice(0, 255);
    }
  };

  const onReply = (id) => {
    if (document.getElementById(id) !== null) {
      if (document.getElementById(id).style.display === "flex") {
        document.getElementById(id).style.display = "none";
      } else {
        document.getElementById(id).style.display = "flex";
      }
    }
  };

  return (
    <React.Fragment>
      <Grid
        container
        sx={{
          width: { xs: "90%", md: "60%" },
          margin: "5rem auto 2rem auto",
        }}
      >
        <Grid
          item
          xs={12}
          sx={{ display: "flex", justifyContent: "space-between" }}
        >
          <Link className="goBack" onClick={onClickMain}>
            <ArrowLeft stroke="#647196" />
            &nbsp;&nbsp;&nbsp; Go Back
          </Link>

          <BlueButton onClick={onClickEditFeedback}>Edit Feedback</BlueButton>
        </Grid>
      </Grid>
      <Grid
        container
        sx={{
          background: "white",
          padding: "2rem",
          borderRadius: "10px",
          width: { xs: "90%", md: "60%" },
          margin: "2rem auto",
        }}
      >
        <Grid
          item
          xs
          sx={{
            textAlign: "left",
          }}
        >
          <UpvoteButton
            variant="contained"
            onClick={() => onClickUpvote(data.id)}
          >
            <ArrowUp />
            {data.upvotes}
          </UpvoteButton>
        </Grid>
        <Grid item xs={9} sx={{ textAlign: "left" }}>
          <Typography sx={{ pb: "10px" }}>
            <b>{data.title}</b>
          </Typography>
          <Typography sx={{ pb: "10px", color: "#647196" }}>
            {data.description}
          </Typography>
          <CategoryButton variant="contained">
            {data.category === "ui" || data.category === "ux"
              ? data.category.toUpperCase()
              : data.category}
          </CategoryButton>
        </Grid>
        <Grid
          item
          xs
          sx={{
            justifyContent: "end",
            display: "flex",
            alignItems: "center",
          }}
        >
          <Comments />
          &nbsp;&nbsp;
          <Typography>
            {data.comments !== undefined ? data.comments.length : 0}
          </Typography>
        </Grid>
      </Grid>

      <Grid
        container
        sx={{
          background: "white",
          padding: "2rem",
          borderRadius: "10px",
          width: { xs: "90%", md: "60%" },
          margin: "2rem auto",
        }}
      >
        <Grid
          item
          xs={12}
          sx={{
            textAlign: "left",
          }}
        >
          <Typography sx={{ fontSize: "1.1rem" }}>
            <b>
              {data.comments !== undefined ? data.comments.length : 0}
              &nbsp;Comments
            </b>
          </Typography>
        </Grid>

        {data.comments !== undefined &&
          data.comments.map((comment) => (
            <Box key={comment.id} sx={{ width: "100%" }}>
              <Grid
                item
                xs={12}
                sx={{
                  textAlign: "left",
                  margin: "2rem 0 1rem 0",
                  display: "flex",
                  alignItems: "center",
                }}
              >
                <img
                  src={require(`${comment.user.image}`)}
                  className="userImage"
                  alt={comment.user.image}
                ></img>
                <Box
                  sx={{
                    width: "100%",
                    display: "flex",
                    justifyContent: "space-between",
                  }}
                >
                  <Box>
                    <b>{comment.user.name}</b>
                    <br />
                    <Box sx={{ color: "#647196", fontSize: "0.9rem" }}>
                      @{comment.user.username}
                    </Box>
                  </Box>
                  <Button onClick={() => onReply(`comment${comment.id}`)}>
                    Reply
                  </Button>
                </Box>
              </Grid>
              <Grid item xs={12} sx={{ textAlign: "left" }}>
                <Box
                  sx={{
                    ml: { sm: "0", md: "5rem" },
                    color: "#647196",
                  }}
                >
                  {comment.content}
                </Box>
                <Box
                  id={`comment${comment.id}`}
                  sx={{
                    display: "none",
                    justifyContent: "space-between",
                    flexDirection: { xs: "column", md: "row" },
                  }}
                >
                  <CustomTextField
                    id={comment.id.toString()}
                    variant="outlined"
                    placeholder="Type your comment here"
                    multiline
                    rows={3}
                    onChange={onChangeReply}
                    sx={{
                      m: { sm: "1rem 0", md: "1rem 1rem 1rem 5rem" },
                      width: { xs: "auto", md: "100%" },
                    }}
                  />
                  <PurpleButton
                    sx={{
                      m: { xs: "1rem 1rem 1rem 5rem", md: "1rem 0" },
                      height: "2.8rem",
                      alignSelf: { xs: "end", md: "inherit" },
                      width: { xs: "auto", md: "14rem", lg: "11rem" },
                    }}
                    onClick={() => onClickReply(comment.id.toString(), "")}
                  >
                    Post Reply
                  </PurpleButton>
                </Box>
                {comment.replies !== undefined &&
                  comment.replies.map((reply, idx) => (
                    <Box key={idx} sx={{ ml: "2.5rem" }}>
                      <Grid
                        item
                        xs={12}
                        sx={{
                          textAlign: "left",
                          margin: "2rem 0 1rem 0",
                          display: "flex",
                          alignItems: "center",
                        }}
                      >
                        <img
                          src={require(`${reply.user.image}`)}
                          className="userImage"
                          alt={comment.user.image}
                        ></img>
                        <Box
                          sx={{
                            width: "100%",
                            display: "flex",
                            justifyContent: "space-between",
                          }}
                        >
                          <Box>
                            <b>{reply.user.name}</b>
                            <br />
                            <Box sx={{ color: "#647196", fontSize: "0.9rem" }}>
                              @{reply.user.username}
                            </Box>
                          </Box>
                          <Button onClick={() => onReply(`reply${idx}`)}>
                            Reply
                          </Button>
                        </Box>
                      </Grid>
                      <Grid
                        item
                        xs={12}
                        sx={{ textAlign: "left", color: "#647196" }}
                      >
                        <Box sx={{ ml: { xs: 0, md: "5rem" } }}>
                          <Box
                            sx={{
                              display: "inline-block",
                              color: "#AD1FEA",
                              fontWeight: 900,
                            }}
                          >
                            @{reply.replyingTo}
                          </Box>
                          &nbsp;
                          {reply.content}
                        </Box>
                        <Box
                          id={`reply${idx}`}
                          sx={{
                            display: "none",
                            flexDirection: { xs: "column", md: "row" },
                          }}
                        >
                          <CustomTextField
                            id={comment.id.toString()}
                            variant="outlined"
                            placeholder="Type your comment here"
                            multiline
                            rows={3}
                            onChange={onChangeReply}
                            sx={{
                              m: { sm: "1rem 0", md: "1rem 1rem 1rem 5rem" },
                              width: { xs: "auto", md: "100%" },
                            }}
                          />
                          <PurpleButton
                            sx={{
                              m: { xs: "1rem 1rem 1rem 5rem", md: "1rem 0" },
                              height: "2.8rem",
                              alignSelf: { xs: "end", md: "inherit" },
                              width: { xs: "auto", md: "14rem", lg: "11rem" },
                            }}
                            onClick={() =>
                              onClickReply(
                                comment.id.toString(),
                                reply.user.username
                              )
                            }
                          >
                            Post Reply
                          </PurpleButton>
                        </Box>
                      </Grid>
                    </Box>
                  ))}
              </Grid>
            </Box>
          ))}
      </Grid>
      <Grid
        container
        sx={{
          background: "white",
          padding: "2.5rem",
          borderRadius: "10px",
          width: { xs: "90%", md: "60%" },
          margin: "2rem auto",
        }}
      >
        <Grid item xs={12} sx={{ textAlign: "left" }}>
          <Typography>
            <b>Add Comment</b>
          </Typography>
        </Grid>
        <Grid item xs={12} sx={{ textAlign: "left", margin: "1.5rem 0" }}>
          <CustomTextField
            id="outlined"
            variant="outlined"
            placeholder="Type your comment here"
            multiline
            rows={3}
            onChange={onChangeComment}
          />
        </Grid>
        <Grid
          item
          xs={12}
          sx={{
            textAlign: "left",
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
          }}
        >
          <Typography sx={{ color: "#647196" }}>
            {255 - comment.length} Characters left
          </Typography>
          <PurpleButton onClick={onClickComment}>Post Comment</PurpleButton>
        </Grid>
      </Grid>
    </React.Fragment>
  );
}
