import { Grid, Typography, Box } from "@mui/material";
import { styled } from "@mui/material/styles";
import React from "react";
import { VscCircleFilled } from "react-icons/vsc";
import { IconContext } from "react-icons";
import { Link } from "react-router-dom";
import { NavBulletPoint } from "./StyledElements";

const RoadmapBox = styled(Box)(({ theme }) => ({
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  paddingBottom: "0.3rem",
}));

const RoadmapCount = styled(Typography)(({ theme }) => ({
  fontSize: "0.9em !important",
  fontWeight: 700,
}));

export default function NavRoadmap({ roadmap, onClickRoadmap }) {
  return (
    <Grid
      item
      xs={12}
      sm
      md={12}
      sx={{
        backgroundColor: "#ffffff",
        padding: "20px 20px 0 20px",
        margin: { xs: "1rem", sm: "0" },
        borderRadius: { xs: 0, sm: "10px" },
      }}
    >
      <RoadmapBox sx={{ paddingBottom: "1.5rem" }}>
        <Typography sx={{ fontWeight: 800 }}>Roadmap</Typography>
        <Link onClick={onClickRoadmap}>
          <Typography sx={{ fontSize: "0.8em !important", color: "blue" }}>
            View
          </Typography>
        </Link>
      </RoadmapBox>
      {roadmap !== undefined &&
        roadmap.map((e, idx) => (
          <RoadmapBox key={idx}>
            <NavBulletPoint>
              <IconContext.Provider value={{ color: e.color }}>
                <VscCircleFilled />
              </IconContext.Provider>
              &nbsp;&nbsp;{e.status}
            </NavBulletPoint>
            <RoadmapCount>{e.count}</RoadmapCount>
          </RoadmapBox>
        ))}
      <br />
    </Grid>
  );
}
