import { Grid, Typography, MenuItem } from "@mui/material";
import React from "react";
import { useMediaQuery } from "react-responsive";
import { ReactComponent as Suggestions } from "./assets/icon-suggestions.svg";
import { ReactComponent as Plus } from "./assets/icon-plus.svg";
import MobileFeedbacks from "./MobileFeedbacks";
import DesktopFeedbacks from "./DesktopFeedbacks";
import NoFeedback from "./NoFeedback";
import { PurpleButton } from "./StyledButton";
import { NavSelect } from "./StyledInput";

export default function Feedbacks({
  data,
  onSelectChange,
  onClickCategory,
  onClickUpvote,
  onClickFeedback,
  onClickNewFeedback,
}) {
  const isMobile = useMediaQuery({ query: `(max-width: 599px)` });

  return (
    <React.Fragment>
      <Grid
        container
        sx={{
          width: "auto",
          backgroundColor: "#3A4374",
          borderRadius: { xs: 0, sm: "10px" },
          alignItems: "center",
          display: "flex",
          padding: "0.5rem 2rem",
          marginTop: { xs: 0, sm: "3rem", md: 0 },
        }}
      >
        <Grid
          item
          sm
          md={3.5}
          sx={{
            textAlign: "left",
            display: { xs: "none", sm: "flex" },
            alignItems: "center",
          }}
        >
          <Suggestions />
          &nbsp;&nbsp;
          <Typography
            sx={{
              color: "#FFFFFF",
              fontWeight: 800,
            }}
          >
            {data.selectedData.length} Suggestions
          </Typography>
        </Grid>
        <Grid
          item
          xs={6}
          sm
          md
          sx={{
            textAlign: "left",
            display: "flex",
            alignItems: "center",
          }}
        >
          <Typography sx={{ color: "#F7E8FD", fontSize: "0.8rem !important" }}>
            Sort by:
          </Typography>
          <NavSelect
            labelId="select-label"
            id="select"
            value={data.sortBy}
            onChange={onSelectChange}
            sx={{ fontSize: "0.8rem !important" }}
          >
            <MenuItem value={"mostUpvotes"}>Most Upbvotes</MenuItem>
            <MenuItem value={"leastUpvotes"}>Least Upvotes</MenuItem>
            <MenuItem value={"mostComments"}>Most Comments</MenuItem>
            <MenuItem value={"leastComments"}>Least Comments</MenuItem>
          </NavSelect>
        </Grid>
        <Grid item xs={6} sm md sx={{ textAlign: "right" }}>
          <PurpleButton onClick={onClickNewFeedback}>
            <Plus />
            &nbsp; Add Feedback
          </PurpleButton>
        </Grid>
      </Grid>
      {data.selectedData.length === 0 ? (
        <NoFeedback />
      ) : (
        data.selectedData.map((fb, idx) =>
          isMobile === false ? (
            <DesktopFeedbacks
              key={idx}
              data={fb}
              sortBy={data.sortBy}
              onClickCategory={onClickCategory}
              onClickUpvote={onClickUpvote}
              onClickFeedback={onClickFeedback}
            />
          ) : (
            <MobileFeedbacks
              key={idx}
              data={fb}
              sortBy={data.sortBy}
              onClickCategory={onClickCategory}
              onClickUpvote={onClickUpvote}
              onClickFeedback={onClickFeedback}
            />
          )
        )
      )}
    </React.Fragment>
  );
}
