import { Grid, Box, Typography } from "@mui/material";
import { Link } from "react-router-dom";
import { ReactComponent as Comments } from "./assets/icon-comments.svg";
import { ReactComponent as ArrowUp } from "./assets/icon-arrow-up.svg";
import React from "react";
import { CategoryButton, UpvoteButton } from "./StyledButton";

export default function MobileFeedbacks({
  data,
  sortBy,
  onClickCategory,
  onClickUpvote,
  onClickFeedback,
}) {
  return (
    <Grid
      container
      sx={{
        width: "auto",
        background: "white",
        padding: 3,
        margin: { xs: "2rem 1.5rem", sm: "1.5rem 0" },
        borderRadius: "10px",
      }}
    >
      <Grid item xs={12} sx={{ textAlign: "left" }}>
        <Box onClick={() => onClickFeedback(data)}>
          <Typography sx={{ pb: "10px", color: "black" }}>
            <b>{data.title}</b>
          </Typography>
          <Typography sx={{ pb: "10px", color: "#647196" }}>
            {data.description}
          </Typography>
        </Box>
        <CategoryButton
          variant="contained"
          value={data.category}
          onClick={onClickCategory}
        >
          {data.category === "ui" || data.category === "ux"
            ? data.category.toUpperCase()
            : data.category}
        </CategoryButton>
      </Grid>
      <Grid
        item
        xs
        sx={{
          mt: 2,
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
        }}
      >
        <UpvoteButton
          value={sortBy}
          onClick={() => onClickUpvote(data.id)}
          sx={{ width: "5rem", display: "flex", flexDirection: "row" }}
        >
          <ArrowUp />
          <Typography sx={{ pl: 1, fontSize: "0.8rem", fontWeight: "bold" }}>
            {data.upvotes}
          </Typography>
        </UpvoteButton>

        <Box
          sx={{
            display: "flex",
            alignItems: "center",
          }}
          onClick={() => onClickFeedback(data)}
        >
          <Comments />
          &nbsp;
          {data.comments !== undefined ? data.comments.length : 0}
        </Box>
      </Grid>
    </Grid>
  );
}
