import {
  Box,
  Grid,
  Typography,
  IconButton,
  Drawer,
  Divider,
} from "@mui/material";
import { styled } from "@mui/material/styles";
import MenuIcon from "@mui/icons-material/Menu";
import React from "react";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import { useMediaQuery } from "react-responsive";
import NavCategory from "./NavCategory";
import NavRoadmap from "./NavRoadmap";

const DrawerHeader = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  justifyContent: "flex-start",
}));

export default function Nav({
  data,
  roadmap,
  onClickCategory,
  onClickRoadmap,
}) {
  const [open, setOpen] = React.useState(false);
  const isMobile = useMediaQuery({ query: `(max-width: 599px)` });

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <Grid container>
      <Grid
        item
        xs={12}
        sm
        md={12}
        sx={{
          minHeight: "100%",
          width: "auto",
          height: { sm: "auto", md: "14vh" },
          textAlign: "left",
          display: "flex",
          flexDirection: "column",
          justifyContent: "end",
          padding: "20px 20px",
          borderRadius: { xs: 0, sm: "10px" },
          position: "relative",
        }}
        className="mainBox"
      >
        <Typography sx={{ color: "#FFFFFF", fontWeight: "800" }}>
          Frontend Mentor
        </Typography>
        <Typography sx={{ color: "#F7E8FD", fontSize: "0.8rem !important" }}>
          Feedback Board
        </Typography>

        <IconButton
          size="large"
          aria-label="open drawer"
          sx={{
            color: "white",
            position: "absolute",
            top: 0,
            bottom: 0,
            right: 10,
            display: { xs: "inherit", sm: "none" },
          }}
          onClick={handleDrawerOpen}
        >
          <MenuIcon />
        </IconButton>
      </Grid>
      {isMobile === false && <NavCategory onClick={onClickCategory} />}
      {isMobile === false && (
        <NavRoadmap roadmap={roadmap} onClickRoadmap={onClickRoadmap} />
      )}
      <Drawer anchor="right" open={open}>
        <DrawerHeader>
          <IconButton onClick={handleDrawerClose}>
            <ChevronRightIcon />
          </IconButton>
        </DrawerHeader>
        <Divider />
        <Box sx={{ width: 250, height: "100%", backgroundColor: "#f2f4ff" }}>
          <NavCategory onClick={onClickCategory} />
          <NavRoadmap roadmap={roadmap} onClickRoadmap={onClickRoadmap} />
        </Box>
      </Drawer>
    </Grid>
  );
}
