import React, { useReducer, useEffect } from "react";
import FeedbackBaord from "./FeedbackBoard";
import Feedback from "./Feedback";
import Roadmap from "./Roadmap";
import EditFeedback from "./EditFeedback";
import NewFeedback from "./NewFeedback";
import { ThemeProvider, createTheme } from "@mui/material/styles";

const theme = createTheme();

export default function Main() {
  const initialState = {
    msg: "",
    allData: [],
    selectedData: [],
    selectedStatus: [],
    sortBy: "mostUpvotes",
    contactServer: false,
    main: true,
    feedback: false,
    roadmap: false,
    editFeedback: false,
    newFeedback: false,
    selectedFeedback: [],
  };

  const initialStatus = [
    { status: "planned", count: 0, color: "orange" },
    { status: "in-progress", count: 0, color: "purple" },
    { status: "live", count: 0, color: "skyblue" },
  ];

  const reducer = (state, newState) => ({ ...state, ...newState });
  const [state, setState] = useReducer(reducer, initialState);
  const [status, setStatus] = React.useState(initialStatus);

  const fetchData = async () => {
    try {
      setState({
        contactServer: true,
      });
      let res = await fetch("/api/data");
      let json = await res.json();

      let status = {};
      json.productRequests.forEach((r) => {
        if (status[r.status] === undefined) {
          status[r.status] = 1;
        } else {
          status[r.status]++;
        }
      });

      let sortedData = json.productRequests.sort((a, b) =>
        a.upvotes < b.upvotes ? 1 : -1
      );

      countRoadmap(sortedData);

      if (state.feedback === true) {
        let feedbackIndex = sortedData.findIndex(
          (fb) => fb.id === state.selectedFeedback.id
        );
        setState({
          selectedFeedback: sortedData[feedbackIndex],
        });
      }

      setState({
        allData: json,
        selectedData: sortedData,
        selectedStatus: status,
        contactServer: true,
      });
    } catch (error) {
      console.log(error);
      setState({
        msg: `Problem loading server data - ${error.message}`,
      });
    }
  };

  useEffect(() => {
    fetchData();
  }, []);
  const onClickCategory = (event) => {
    if (event.target.value === "all") {
      setState({
        selectedData: state.allData.productRequests,
      });

      countRoadmap(state.allData.productRequests);
    } else {
      let data = state.allData.productRequests.filter(
        (r) => r.category === event.target.value
      );

      countRoadmap(data);
      setState({
        selectedData: data,
      });
    }
  };

  const countRoadmap = (data) => {
    const status = initialStatus;

    data.forEach((request) => {
      if (
        status.find(({ status }) => status === request.status) !== undefined
      ) {
        status.find(({ status }) => status === request.status).count++;
      }
    });

    setStatus(status);
  };

  const onSelectChange = (event) => {
    var sortedData = [];
    if (event.target.value === "mostUpvotes") {
      sortedData = state.allData.productRequests.sort((a, b) =>
        a.upvotes < b.upvotes ? 1 : -1
      );
    } else if (event.target.value === "leastUpvotes") {
      sortedData = state.allData.productRequests.sort((a, b) =>
        a.upvotes > b.upvotes ? 1 : -1
      );
    } else if (event.target.value === "mostComments") {
      sortedData = state.allData.productRequests.sort((a, b) =>
        (a.comments === undefined ? 0 : a.comments.length) <
        (b.comments === undefined ? 0 : b.comments.length)
          ? 1
          : -1
      );
    } else if (event.target.value === "leastComments") {
      sortedData = state.allData.productRequests.sort((a, b) =>
        (a.comments === undefined ? 0 : a.comments.length) >
        (b.comments === undefined ? 0 : b.comments.length)
          ? 1
          : -1
      );
    }

    setState({
      selectedData: sortedData,
      sortBy: event.target.value,
    });
  };

  const onClickUpvote = async (id) => {
    let myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    try {
      let res = await fetch(`/editUpvote/${id}`, {
        method: "PUT",
        headers: myHeaders,
      });

      let json = await res.json();
      console.log(json);

      fetchData();
    } catch (error) {}
  };

  const onClickFeedback = (data) => {
    setState({
      selectedFeedback: data,
      main: false,
      feedback: true,
      roadmap: false,
      editFeedback: false,
      newFeedback: false,
    });
  };

  const onClickRoadmap = () => {
    setState({
      main: false,
      feedback: false,
      roadmap: true,
      editFeedback: false,
      newFeedback: false,
    });
  };

  const onClickMain = () => {
    setState({
      main: true,
      feedback: false,
      roadmap: false,
      editFeedback: false,
      newFeedback: false,
    });
  };

  const onClickEditFeedback = () => {
    setState({
      main: false,
      feedback: false,
      roadmap: false,
      editFeedback: true,
      newFeedback: false,
    });
  };

  const onClickNewFeedback = () => {
    setState({
      main: false,
      feedback: false,
      roadmap: false,
      editFeedback: false,
      newFeedback: true,
    });
  };

  return (
    <ThemeProvider theme={theme}>
      {state.feedback && (
        <Feedback
          data={state.selectedFeedback}
          onClickMain={onClickMain}
          onClickUpvote={onClickUpvote}
          onClickEditFeedback={onClickEditFeedback}
        />
      )}
      {state.roadmap && (
        <Roadmap
          data={state.selectedData}
          onClickMain={onClickMain}
          onClickCategory={onClickCategory}
          onClickUpvote={onClickUpvote}
          onClickFeedback={onClickFeedback}
        />
      )}
      {state.editFeedback && (
        <EditFeedback
          data={state.selectedFeedback}
          onClickMain={onClickMain}
          onClickFeedback={onClickFeedback}
          fetchData={fetchData}
        />
      )}
      {state.newFeedback && (
        <NewFeedback
          data={state.allData.productRequests}
          onClickMain={onClickMain}
          fetchData={fetchData}
        />
      )}
      {state.main && (
        <FeedbackBaord
          data={state}
          roadmap={status}
          onClickCategory={onClickCategory}
          onSelectChange={onSelectChange}
          onClickUpvote={onClickUpvote}
          onClickFeedback={onClickFeedback}
          onClickRoadmap={onClickRoadmap}
          onClickNewFeedback={onClickNewFeedback}
        />
      )}
    </ThemeProvider>
  );
}
