import { Grid, Typography, MenuItem } from "@mui/material";
import React, { useReducer } from "react";
import { ReactComponent as ArrowLeft } from "./assets/icon-arrow-left.svg";
import { ReactComponent as EditIcon } from "./assets/icon-edit-feedback.svg";
import { Link } from "react-router-dom";
import { PurpleButton, NavyButton, RedButton } from "./StyledButton";
import { CustomTextField, AddEditSelect } from "./StyledInput";
import { Description, Title } from "./StyledElements";

export default function EditFeedback({
  data,
  onClickMain,
  onClickFeedback,
  fetchData,
}) {
  const initialState = {
    id: data.id,
    title: data.title,
    category: data.category,
    upvotes: data.upvotes,
    status: data.status,
    description: data.description,
    comments: data.comments,
  };

  const reducer = (state, newState) => ({ ...state, ...newState });
  const [state, setState] = useReducer(reducer, initialState);

  const onDeleteClick = async () => {
    let myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    try {
      let response = await fetch(
        `http://localhost:8000/deleteFeedback/${data.id}`,
        {
          method: "DELETE",
          headers: myHeaders,
        }
      );
      fetchData();
      onClickMain();
    } catch (error) {}
  };

  const onAddClick = async () => {
    let feedback = {
      id: state.id,
      title: state.title,
      category: state.category,
      upvotes: state.upvotes,
      status: state.status,
      description: state.description,
      comments: state.comments,
    };

    let feedbackStr = JSON.stringify(feedback);
    let myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    try {
      let response = await fetch(
        `http://localhost:8000/editFeedback/${data.id}`,
        {
          method: "PUT",
          headers: myHeaders,
          body: feedbackStr,
        }
      );

      fetchData();
      onClickFeedback(feedback);
    } catch (error) {}
  };

  const onTitleChange = (e) => {
    setState({ title: e.target.value });
  };

  const onDescriptionChange = (e) => {
    setState({ description: e.target.value });
  };

  const onCategoryChange = (e) => {
    setState({ category: e.target.value });
  };

  const onStatusChange = (e) => {
    setState({ status: e.target.value });
  };

  return (
    <React.Fragment>
      <Grid
        container
        sx={{
          width: "60%",
          margin: "5rem auto 2rem auto",
        }}
      >
        <Grid
          item
          xs={12}
          sx={{ display: "flex", justifyContent: "space-between" }}
        >
          <Link className="goBack" onClick={onClickMain}>
            <ArrowLeft stroke="#647196" />
            &nbsp;&nbsp;&nbsp; Go Back
          </Link>
        </Grid>
      </Grid>
      <Grid
        container
        sx={{
          background: "white",
          padding: "3rem 2rem",
          borderRadius: "10px",
          width: "60%",
          margin: "3.5rem auto",
          textAlign: "left",
          position: "relative",
        }}
      >
        <EditIcon style={{ position: "absolute", top: "-1rem" }} />
        <Grid item xs={12}>
          <Typography sx={{ fontSize: "1.4rem", mb: 5 }}>
            <b>Editing 'Add a dark theme option'</b>
          </Typography>
        </Grid>
        <Grid item xs={12} sx={{ mb: 2 }}>
          <Title>Feedback Title</Title>
          <Description>Add a short, description headline</Description>
          <CustomTextField
            id="outlined"
            variant="outlined"
            multiline
            rows={1}
            value={state.title}
            onChange={onTitleChange}
          />
        </Grid>
        <Grid item xs={12} sx={{ mb: 2 }}>
          <Title>Category</Title>
          <Description>Choose a category for your feedback</Description>
          <AddEditSelect
            labelId="select-label"
            id="select"
            value={state.category}
            onChange={onCategoryChange}
          >
            <MenuItem value="feature">Feature</MenuItem>
            <MenuItem value="ui">UI</MenuItem>
            <MenuItem value="ux">UX</MenuItem>
            <MenuItem value="enhancement">Enhancement</MenuItem>
            <MenuItem value="bug">Bug</MenuItem>
          </AddEditSelect>
        </Grid>
        <Grid item xs={12} sx={{ mb: 2 }}>
          <Title>Update Status</Title>
          <Description>Change feedback state</Description>
          <AddEditSelect
            labelId="select-label"
            id="select"
            value={state.status}
            onChange={onStatusChange}
          >
            <MenuItem value="suggestion">Suggestion</MenuItem>
            <MenuItem value="planned">Planned</MenuItem>
            <MenuItem value="inProgress">In-Progress</MenuItem>
            <MenuItem value="live">Live</MenuItem>
          </AddEditSelect>
        </Grid>
        <Grid item xs={12}>
          <Title>Feedback Detail</Title>
          <Description>
            Include any specific comments on what should be improved, added,
            etc.
          </Description>
          <CustomTextField
            id="outlined"
            variant="outlined"
            multiline
            rows={3}
            value={state.description}
            onChange={onDescriptionChange}
          />
        </Grid>
        <Grid
          item
          xs={12}
          sx={{ display: "flex", justifyContent: "end", mt: 5 }}
        >
          <RedButton sx={{ mr: "auto" }} onClick={onDeleteClick}>
            Delete
          </RedButton>
          <NavyButton onClick={onClickMain} sx={{ mr: 2 }}>
            Cancel
          </NavyButton>
          <PurpleButton onClick={onAddClick}>Add Feedback</PurpleButton>
        </Grid>
      </Grid>
    </React.Fragment>
  );
}
